import React from 'react';
import { Dialog, DialogTitle, DialogContent, DialogContentText, DialogActions, Button, Typography, Grid, TextField } from '@material-ui/core';
import bowser from 'bowser';
import css from './styles.css';

const styles ={
    input: {
        textAlign: 'right',
        color: 'black',
    },
}

const partialGood = /^-?([0-9]*)?(\.[0-9]*)?$/;
const good = /^-?[0-9]+(\.[0-9]+)?$/;

export default
class NumberEditor extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            value: props.defaultValue || '',            
        }
    }

    componentDidUpdate(prevProps) {
        if(this.props.open != prevProps.open || this.props.defaultValue != prevProps.defaultValue)
            this.setState({ value: this.props.defaultValue || '' });
    }

    render() {
        const isMobile = bowser.mobile || bowser.tablet;
        return (
			<Dialog open={this.props.open} onClose={this.props.onRequestClose}>								
				<DialogContent>						
					<Typography variant="body2"><b>{this.props.caption}</b></Typography>
                    <TextField fullWidth autoFocus={!isMobile} disabled={isMobile} value={this.state.value} inputProps={{ style: styles.input }} onChange={this.handleChange} onKeyDown={this.handleInputKeyDown} />
                    <br /><br />
                    <Grid container justify="space-around" alignItems="center" wrap="nowrap">
                        <Grid container item xs={false}><Button onClick={this.handlePad.bind(this,1)} variant="outlined" className={css.padButton}>1</Button></Grid>
                        <Grid container item xs={false}><Button onClick={this.handlePad.bind(this,2)} variant="outlined" classes={{ root: css.padButton }}>2</Button></Grid>
                        <Grid container item xs={false}><Button onClick={this.handlePad.bind(this,3)} variant="outlined" classes={{ root: css.padButton }}>3</Button></Grid>
                    </Grid>
                    <Grid container justify="space-around" alignItems="center" wrap="nowrap">
                        <Grid container item xs={false}><Button onClick={this.handlePad.bind(this,4)} variant="outlined" classes={{ root: css.padButton }}>4</Button></Grid>
                        <Grid container item xs={false}><Button onClick={this.handlePad.bind(this,5)}variant="outlined" classes={{ root: css.padButton }}>5</Button></Grid>
                        <Grid container item xs={false}><Button onClick={this.handlePad.bind(this,6)} variant="outlined" classes={{ root: css.padButton }}>6</Button></Grid>
                    </Grid>
                    <Grid container justify="space-around" alignItems="center" wrap="nowrap">
                        <Grid container item xs={false}><Button onClick={this.handlePad.bind(this,7)} variant="outlined" classes={{ root: css.padButton }}>7</Button></Grid>
                        <Grid container item xs={false}><Button onClick={this.handlePad.bind(this,8)} variant="outlined" classes={{ root: css.padButton }}>8</Button></Grid>
                        <Grid container item xs={false}><Button onClick={this.handlePad.bind(this,9)} variant="outlined" classes={{ root: css.padButton }}>9</Button></Grid>                        
                    </Grid>
                    <Grid container justify="space-around" alignItems="center" wrap="nowrap">
                        <Grid container item xs={false}><Button onClick={this.handlePad.bind(this,'<')} variant="outlined" classes={{ root: css.padButton }}>{'<'}</Button></Grid>
                        <Grid container item xs={false}><Button onClick={this.handlePad.bind(this,0)} variant="outlined" classes={{ root: css.padButton }}>0</Button></Grid>
                        <Grid container item xs={false}><Button onClick={this.handlePad.bind(this,'.')} variant="outlined" classes={{ root: css.padButton }}>{'.'}</Button></Grid>                        
                    </Grid>
				</DialogContent>				                
                <DialogActions>
                    <Button onClick={this.handleOkClick} disabled={!good.test(this.state.value)}>{'OK'}</Button>
                    <Button onClick={this.handleCancelClick}>{'Отмена'}</Button>
                </DialogActions>
			</Dialog>
        );
    }

    handleOkClick = (event) => {
        if(!good.test(this.state.value))
            return;
        this.props.onFinish && this.props.onFinish(Number(this.state.value));
        this.props.onRequestClose && this.props.onRequestClose();
    }

    handleCancelClick = (event) => {
        this.props.onRequestClose && this.props.onRequestClose();
    }

    handleChange = (event) => {
        const value = event.target.value;        
        if(!partialGood.test(value))
            return;
        this.setState({ value });
    }

    handleInputKeyDown = (event) => {
        if(event.keyCode == 13)
            this.handleOkClick();
    }

    handlePad = (action) => {
        let value = this.state.value;
        switch(action) {
            case '<':
                value = value.slice(0,-1);
                break;
            case '.':                
                value = value == '' ? '0.' : value + '.';
                break;
            case 0:                
                if(value !== '0')
                    value = value + '0';
                break;
            default:                
                value = value === '0' ? action + '' : value + action;
        }        
        if(!partialGood.test(value))
            return;
        this.setState({ value });

    }
}