import React from 'react';
import { Listener } from '../store';
import { Grid, TextField, Typography, Button, Table, TableHead, TableRow, TableCell, TableBody, FormControlLabel, Checkbox  } from '@material-ui/core';
import { Lock, LockOpen  } from '@material-ui/icons';
import { Center, Panel, Hr, Busy, BackButton, NumberEditor } from '../ui';
import { Link } from 'react-router-dom';

import css from './styles.css';

let styles = {
	tableBlock: {
		display: 'block',
		overflowX: 'auto',
		whiteSpace: 'nowrap',
		width: '100%',
	},
	table: {
	},
	headerRow: {
		height: '32px',
	},
	row: {
		height: '32px',
		cursor: 'pointer',
	},	
	headerCell: {
		padding: '0px 4px',		
		whiteSpace: 'nowrap',		
		border: '1px solid rgba(224, 224, 224, 1)',
		textAlign: 'right',	
		minWidth: '32px',
	},
	cell: {
		padding: '0px 4px',
		whiteSpace: 'nowrap',
		textAlign: 'right',		
		border: '1px solid rgba(224, 224, 224, 1)',
	},
	count: {
		
	},
	countInput: {
		width: '40px',
		textAlign: 'right',
	},	
}

function checkedNumber(n) {
	const parsed = Number.parseInt(n);
	return parsed == n && parsed > 0 ? parsed : undefined;
}

function range(n) {
	const result = new Array(n);
	for(let i=0; i<n; ++i)
		result[i] = i;
	return result;
}

const maxCount = 99;
const partialGoodNumber = /^[0-9]*$/;

export default
class Calibration extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			calibration: null,
			samplesCount: '11',
			measuresCount: '11',				
			comment: '',
			name: '',
			tableData: {},		
			editOpen: false,
			editCaption: '',
			editFinish: null,			
		};
	}

	renderTable() {
		const N = checkedNumber(this.state.measuresCount);
		const M = checkedNumber(this.state.samplesCount);
		if(!this.state.calibration || !N || !M || N>maxCount || M>maxCount)
			return;
		const headerCells = range(N).map((index) => {
			return <TableCell key={index} style={styles.headerCell}>{'Изм. '+(index+1)}</TableCell>;
		});
		const header = (
			<TableHead>
				<TableRow style={styles.headerRow}>
					<TableCell style={styles.headerCell}>{'№'}</TableCell>
					<TableCell style={styles.headerCell}>{'Конц'}</TableCell>
					{headerCells}
				</TableRow>
			</TableHead>
		);		
		const rows = range(M).map((rowIndex) => {			
			const rowCells = range(N+1).map((columnIndex) => {
				const dataKey = rowIndex+'$'+columnIndex;
				const onClick = () => {
					this.setState({
						editOpen: true,
						editCaption: 'Образец '+(rowIndex+1)+', измерение '+columnIndex,
						editFinish: (value) => {
							const tableData = {...this.state.tableData};
							tableData[dataKey] = value;
							this.setState({ tableData });
						}
					});
				}
				const value = this.state.tableData[dataKey];
				return <TableCell key={columnIndex} style={styles.cell} onClick={onClick}>{value}</TableCell>
			});
			return (
				<TableRow key={rowIndex} style={styles.row} classes={{ root: css.tableRow }}>
					<TableCell style={styles.cell} variant="head">{rowIndex+1}</TableCell>	
					{rowCells}				
				</TableRow>
			);
		});	
		return (
			<div style={styles.tableBlock}>
				<Table style={styles.table}>
						{header}
						<TableBody>
							{rows}
						</TableBody>				
				</Table>
			</div>
		);	
	}	

	render() {
		const renderedTable = this.renderTable();
		const calculateDisabled = false;
		return <>
			<Busy show={this.state.calibration == null} block />
			<Listener reload name={'calibration.'+this.props.match.params.calibration_id} onChange={this.handleCalibrationChange} />
			<NumberEditor caption={this.state.editCaption} open={this.state.editOpen} onRequestClose={this.handleEditorRequestClose} onFinish={this.state.editFinish} />
			<Center hor ver>
				<Panel width="720px" maxWidth="100%">				
					<Center hor><Typography variant="headline">{'Новая градуировка'}</Typography></Center>
					<br />
					<Grid container justify="space-between" alignItems="center">
						<Grid item xs={false}>
							<Typography variant="body2">{'Определяемый показатель'}</Typography>							
						</Grid>
						<Grid item xs={false}>
							<Typography variant="body2"><b>{this.state.calibration && this.state.calibration.character}</b></Typography>
						</Grid>
					</Grid>										
					<Grid container justify="space-between" alignItems="center">
						<Grid item xs={false}>
							<Typography variant="body2">{'Сигнал в'}</Typography>							
						</Grid>
						<Grid item xs={false}>
							<Typography variant="body2"><b>{this.state.calibration && this.state.calibration.signal}</b></Typography>
						</Grid>
					</Grid>		
					<Grid container justify="space-between" alignItems="center">
						<Grid item xs={false}>
							<Typography variant="body2">{'Концентрация в'}</Typography>							
						</Grid>
						<Grid item xs={false}>
							<Typography variant="body2"><b>{this.state.calibration && this.state.calibration.concentration}</b></Typography>
						</Grid>
					</Grid>		
					<TextField fullWidth value={this.state.name} label="Название образца" margin="dense" onChange={this.handleNameChange} />
					<TextField fullWidth multiline rows={1} rowsMax={5} value={this.state.comment} label="Примечание" margin="dense" onChange={this.handleCommentChange} />
					<Grid container justify="space-between" alignItems="center">
						<Grid item xs={false}>
							<Typography variant="body2">{'Число образцов сравнения (ОС)'}</Typography>							
						</Grid>
						<Grid item xs={false}>
							<TextField style={styles.count} inputProps={{ style: styles.countInput }} value={this.state.samplesCount} onChange={this.handleSamplesCountChange} />
						</Grid>
					</Grid>										
					<Grid container justify="space-between" alignItems="center">
						<Grid item xs={false}>
							<Typography variant="body2">{'Число измерений для ОС'}</Typography>							
						</Grid>
						<Grid item xs={false}>
							<TextField style={styles.count} inputProps={{ style: styles.countInput }} value={this.state.measuresCount} onChange={this.handleMeasuresCountChange}/>
						</Grid>
					</Grid>		
					<Grid container justify="space-between" alignItems="center">					
						<Grid item xs={false}>
							<BackButton component={Link} to={'/calibrations_list/'+this.props.match.params.methodic_id+'/'+this.props.match.params.page_id} />
						</Grid>
						<Grid item xs={false}>
							<Button variant="outlined" size="small" color="secondary" disabled={calculateDisabled} onClick={this.handleCalculateClick}>{'Рассчитать'}</Button>	
						</Grid>
						<Grid item xs={false}>
							<FormControlLabel control={<Checkbox color="primary" checked={this.state.weighted} onChange={this.handleWeightedChange} />} label="Взвешенный МНК" />
						</Grid>
					</Grid>										
					<Hr />
					{renderedTable}
				</Panel>
			</Center>
		</>;
	}

	handleCalibrationChange = (calibration) => {
		this.setState({ calibration });
	}
	
	handleSamplesCountChange = (event) => {		
		const value = event.target.value;
		if(!partialGoodNumber.test(value))
			return;
		this.setState({ samplesCount: value });
	}

	handleMeasuresCountChange = (event) => {
		const value = event.target.value;
		if(!partialGoodNumber.test(value))
			return;
		this.setState({ measuresCount: value });
	}

	handleEditorRequestClose = () => {
		this.setState({ editOpen: false });
	}

	handleNameChange = (event) => {
		this.setState({ name: event.target.value });
	}

	handleCommentChange = (event) => {
		this.setState({ comment: event.target.value });
	}

	handleCalculateClick = () => {

	}
};